import axios from 'axios'
import { getCookie } from './utils';
import store from '@/store'
import { Toast } from 'vant';




// 创建axios实例
const service = axios.create({
  baseURL: "http://47.101.58.102:8888/shopone/", // api的base_url
})

// respone拦截器
service.interceptors.response.use(
  response => {
    let res = response.data
    console.log(res)
    if(res.code==200000){
      return response
    }else{
      Toast.fail(res.msg);
      return Promise.reject(new Error(res.msg))
    }
    // console.log("fetch response:===>"+JSON.stringify(response));
  },
  (error) => {
    Toast.fail('Net work is unavailable, please check it');
    return Promise.reject(error)
  })

service.interceptors.request.use(config => {

  config.headers['Authorization'] = store.getters.token.access_token  // 让每个请求携带token--['X-Token']为自定义key 请根据实际情况自行修改
  // config.headers["Auth"] = getCookie(tokenName), //当前cookie 用户 token
  config.headers["Content-Type"] = "application/json;charset=UTF-8"
  config.headers["Accept"] = "*/*"

  return config
}, error => {

  console.log(error) // for debug
  return Promise.reject(error)
})


export default service




