import { appUpdate,showHeader,hideHeader } from '@/common/js/app'

const state = {
    appVersion: {},
   
}


const mutations = {
    setAppVersion(state, appVersion) {
        state.appVersion = appVersion;
    }
}


const actions = {
    appVersion({ commit }) {
        return new Promise((resolve, reject) => {
            appUpdate("http://static.ikedou.net/xiaoguo/dist.zip","1","0").then(res => {
            
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },
    showHeader({ commit }) {
        return new Promise((resolve, reject) => {
            showHeader().then(res => {
            
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },
    hideHeader({ commit }) {
        return new Promise((resolve, reject) => {
            hideHeader().then(res => {
            
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    }
}

export default {
    state,
    mutations,
    actions
}



