import { Toast } from 'vant';
function initBridge(callback) {

    if (window.WebViewJavascriptBridge) {
        callback(WebViewJavascriptBridge)
    } else {
        document.addEventListener(
                                  'WebViewJavascriptBridgeReady'
                                  , function() {
                                  callback(WebViewJavascriptBridge)
                                  },
                                  false
                                  );
    }

    if (window.WebViewJavascriptBridge) { return callback(WebViewJavascriptBridge); }
    if (window.WVJBCallbacks) { return window.WVJBCallbacks.push(callback); }
    window.WVJBCallbacks = [callback];
    var WVJBIframe = document.createElement('iframe');
    WVJBIframe.style.display = 'none';
    WVJBIframe.src = 'wvjbscheme://__BRIDGE_LOADED__';
    document.documentElement.appendChild(WVJBIframe);
    setTimeout(function () { document.documentElement.removeChild(WVJBIframe) }, 0)
}

initBridge((bridge) => {
    bridge.registerHandler('testJSFunction', (data, callback) => {
        callback('js执行过了' + data);
    })
});



function callHandler(handlerName, param) {
    console.log(arguments);
    return new Promise((resolve, reject) => {
        try {

            WebViewJavascriptBridge.callHandler(handlerName, JSON.stringify(param),
                    (res) => {
                        try {
                            resolve(res);
                        } catch (e) {
                            Toast.fail(e.message);
                            reject(e);
                        }
                    });
        } catch (e) {
            Toast.fail(e.message);
            reject(e);
        }

    });
}


function registerHandler(handlerName, callback) {
    initBridge((bridge) => {
        bridge.registerHandler(handlerName, (data, resCallback) => {
            console.log(resCallback);
            if (typeof callback == "function") {
                callback(data);
            }
        });
    })
}

