import request from '@/common/js/fetch'

export function login(username, password) {
  const params = {
    account:username,
    password:password
  }
  return request({
    url: '/signIn',
    method: 'post',
    params
  })
}

export function register(params) {
  return request({
    url: '/signUp',
    method: 'post',
    params
  })
}

export function resetPassword(params) {
  return request({
    url: '/user/resetPassword',
    method: 'post',
    params
  })
}


export function logout() {
  return request({
    url: '/signOut',
    method: 'post'
  })
}

//修改密码
export function updatePassword(data) {
  return request({
    url: '/user/update/password',
    method: 'post',
    data:JSON.stringify(data)
  })
}

export function getUserInfo() {
  return request({
    url: '/user/info',
    method: 'get'
  })
}
//获取地址
export function getAddress() {
  return request({
    url: '/address/get',
    method: 'get'
  })
}
//我的地址
export function myAddress() {
  return request({
    url: '/address/myAddress',
    method: 'get'
  })
}
//新增地址
export function addAddress(options) {
  const data={
    firstName:options.firstName,
    lastName:options.lastName,
    countryName:options.countryName,
    countryCode:options.countryCode,
    provinceCode:options.provinceCode,
    provinceName:options.provinceName,
    cityCode:options.cityCode,
    cityCode:options.cityCode,
    street:options.street,
    mobile:options.mobile,
    apartment:options.apartment,
    landmark:options.landmark
  }
  return request({
    url: '/address/add',
    method: 'post',
    params:data
  })
}
